import React, { Component } from 'react';
import { Chart } from "react-google-charts";
import Headerbar from './components/header/header'
import { dataToChart } from './data-source/data-to-chart';
import './App.css';

class App extends Component {
  state = {
    dataToRender: dataToChart[0].data
  };

  render() {

    const colors = ['#e74c3c', '#f39c12', '#940101', '#27ae60', '#ffd209', '#2980b9', '#16a085', '#8f1b51', '#8e44ad', '#0062b3', '#f1c40f', '#1e37c5', '#1ac50b', '#be123d', '#ff8519', '#d35400', '#1abc9c'];

    const newDataHandler = (id) => {
      const dataToFilter = dataToChart;
      //
      dataToFilter.filter((data) => {
        if (data.idData === `${id}`) {
          this.setState({
            dataToRender: data.data
          });
        }
        return id;
      });
    }

    const onHideColorTip = () => {
      !!document.querySelector("#HeaderTipClickColors")
        ? document.querySelector("#HeaderTipClickColors").style.display = 'none'
        : console.clear();
    }

    return (


      <div className={"App-chart"} >


        <Headerbar clicked={(event) => newDataHandler(event)} text={'Bot Negociador:'} subText={'fluxo de acessos'}></Headerbar>
        <Chart
          onClick
          chartType="Sankey"
          options={{
            sankey: {
              link: {
                interactivity: true,
                colorMode: 'gradient',
                colors: colors,
                // color: { stroke: '#333', strokeWidth: 1 },
              },
              node: {
                label: {
                  fontName: 'Sans-Serif',
                  fontSize: 14,
                  color: '#000',
                  bold: false,
                  italic: false
                },
                interactivity: true, // Allows you to select nodes.
                labelPadding: 12,     // Horizontal distance between the label and the node.
                nodePadding: 16,     // Vertical distance between nodes.
                width: 24,            // Thickness of the node.
                colors: colors
              }
            },

          }}
          data={this.state.dataToRender}
          loader={<div className={"App-chart__loader"}>Carregando... por favor, aguarde
            <div className={"sk-chase"}>
              <div className={"sk-chase-dot"}></div>
              <div className={"sk-chase-dot"}></div>
              <div className={"sk-chase-dot"}></div>
              <div className={"sk-chase-dot"}></div>
              <div className={"sk-chase-dot"}></div>
              <div className={"sk-chase-dot"}></div>
            </div>
          </div>}

          width={`${window.innerWidth - 32}px`}
          height={`${window.innerHeight - 64}px`}
          legendToggle
          rootProps={{ 'data-testid': '1' }}

          chartEvents={[
            {
              eventName: 'select',
              callback: ({ chartWrapper }) => {
                onHideColorTip()
              },
            },
          ]}
        />
      </div>
    );
  }
}

export default App;
