import React from 'react';
import './header.css';
import DatePicker, { registerLocale } from "react-datepicker";
import local from "date-fns/locale/pt-BR";
import "react-datepicker/dist/react-datepicker.css";
//
registerLocale("local", local);

class header extends React.Component {
    state = {
        startDate: new Date("2020/09/18"),
        showColorsTip: true,
    };

    handleChange = date => {
        this.props.clicked(date.getTime());
        this.setState({
            startDate: date
        });
    };

    onDisableColorTip = () => {
        this.setState({
            showColorsTip: false,
        });
    }


    render() {
        let colorTipSection;
        //
        this.state.showColorsTip
            ? colorTipSection = (
                <section id="HeaderTipClickColors" className={"Header-tip__clickColors"}>
                    <p><b>DICA: </b>clique nas áreas coloridas para selecioná-las</p>
                    <button type="button" onClick={this.onDisableColorTip}>Ok</button>
                </section>
            )
            : colorTipSection = null;

        return (
            <header className={"Header"}>
                <h1 className={"Header-title"}>{this.props.text}</h1>
                <h2 className={"Header-subTitle"}>{this.props.subText}</h2>

                <DatePicker id="HeaderDatepicker" className={"Header-datepicker"}
                    selected={this.state.startDate}
                    onChange={this.handleChange}
                    locale="local"
                    // YYYY/MM/DD
                    minDate={new Date("2020/09/14")}
                    maxDate={new Date("2020/09/18")}
                    placeholderText="Selecione a data"
                    dateFormat="P"
                />
                <p className={"Header-tip"}>Selecione a data:</p>
                {colorTipSection}

            </header>

        );
    }
}

export default header;